<template>
  <div class="container">
    <!-- ===========
      ======订单管理 =====
    ==================-->
    <!-- 顶部引用 -->
      <APPHeader :flag="3"></APPHeader>
    <!-- 应用管理 -->
    <div class="BackgroundMap">
      <div class="BackgroundTitle">
        <li>钱包管理</li>
      </div>
    </div>

    <!-- 内容展示 -->
    <div class="ContentContainer">
      <div class="IndicatorBar">
        <div class="rqihez">
          <div class="applyName">
            <li>应用名称</li>
          </div>
          <div class="WalletAddress">
            <li>钱包地址</li>
          </div>
          <div class="operation">
            <li>操作</li>
          </div>
        </div>
      </div>

      <!-- 数据渲染-->
      <div class="Renderingdata" v-for="item in walletdata"  v-bind:key="item.id">
        <div class="wjtname">
          <li>{{item.payCoin.coinName}}</li>
        </div>
        <div class="dizhidata">
          <li>{{item.address}}</li>
        </div>
        <div class="caozuo">
          <li @click="ChangeAddress">更换地址</li>
        </div>
      </div>
      
 
    </div>
    
  </div>
</template>

<script>
import APPHeader from '../../src/header'//顶部引入
import {WalletManagement } from "@/api/user";
export default {
  name: "index",
  data() {
    return {
    walletdata:[],
    };
  },
  mounted(){
  this.WalletManagement();
  },

  methods:{
  ChangeAddress(){
    console.log("查看更多")
  },
  WalletManagement(){
    console.log("进入页面触发了")
    let wallet ={};
   WalletManagement(wallet).then(res =>{
     this.walletdata =res.data;
     console.log(this.walletdata)

   })
  }

  },
    components:{
     APPHeader,//顶部引入
  },
};
</script>


<style scoped>

.BackgroundMap {
  height: 170px;
  width: 100%;
  background-image: url(../assets/bg.png);
}

.BackgroundTitle {
  width: 1200px;
  margin: 0 auto;
  position: relative;
  top: 60px;
}
.BackgroundTitle li {
  font-size: 36px;
  font-family: PingFangSC-Semibold, PingFang SC;
  font-weight: 600;
  color: rgba(255, 255, 255, 1);
  line-height: 50px;
  float: left;
}
.BackgroundTitle button {
  float: right;
  width: 108px;
  height: 40px;
  border-radius: 5px;
  border: 1px solid rgba(255, 255, 255, 1);
  font-size: 14px;
  font-family: PingFangSC-Medium, PingFang SC;
  font-weight: 500;
  color: rgba(255, 255, 255, 1);
  line-height: 20px;
  background-color: transparent;
}
.ContentContainer {
  width: 1200px;
  height: 800px;
  margin: 0 auto;
}
.IndicatorBar {
  width: 1200px;
  height: 56px;
  background: #ffffff;
  padding-top: 50px;
}
.IndicatorBar li {
  font-size: 14px;
  font-family: PingFangSC-Regular, PingFang SC;
  font-weight: 400;
  color: rgba(110, 110, 110, 1);
  line-height: 20px;
}
.applyName {
  width: 513px;
  height: 38px;
  padding-top: 18px;
  float: left;
}
.WalletAddress {
  width: 518px;
  height: 38px;
  padding-top: 18px;
  float: left;
}
.operation {
  width: 102px;
  height: 38px;
  padding-top: 18px;
  float: left;
}
.rqihez {
  width: 1133px;
  height: 56px;
  padding-left: 67px;
  background: rgba(245, 245, 245, 1);
}
.Renderingdata {
  width: 1140px;
  height: 55px;
  border: 1px solid rgba(230, 234, 239, 1);
  padding-top: 33px;
  padding-left: 60px;
}
.Renderingdata li {
  font-size: 14px;
  font-family: PingFangSC-Medium, PingFang SC;
  font-weight: 500;
  color: rgba(51, 51, 51, 1);
  line-height: 20px;
}
.wjtname {
  width: 366px;
  height: 55px;
  float: left;
}
.dizhidata {
  width: 360px;
  height: 55px;
  float: left;
}
.dizhidata li{
   text-align: center;
}
.caozuo {
  width: 116px;
  height: 55px;
  float:right;
}
.caozuo li {
  font-size: 14px;
  font-family: PingFangSC-Medium, PingFang SC;
  font-weight: 500;
  color: rgba(49, 140, 255, 1);
  line-height: 20px;
}
</style>



