<script>
import APPHeader from "../../src/header"; //顶部引入  这个可以 我做了  我的意思 要中间弹出来  后来消失 知道了，别动你看  
import {
  WalletManagement,
  ApplicationManagement,
  AssetDetails,
  totalAssets,
  essentialInformation,
} from "@/api/user";
export default {
  name: "index",
  data() {
    return {
      application: [], //应用
      modal2: false, //充值弹窗
      daili:1,
      modal3: false, //体现弹窗
      address: "",
      detailed: [], //应用流水详细
      shuju:[],//渲染支付总金额
      productId: 0, //应用id
      type: 0, //类型3全部 0支付钱包 1应用钱包 2代理钱包
      pageNo: "", //页号1开始
      pageSize: "", //页数里面多少个数据
      f2: 0,
      f1:0,
      yiid:0,
    };
  },

  mounted() {
    this.ApplicationManagement();
    this.DoItNow();
    this.totalAssets();
    this.xuanrshuju();
  },

  methods: {
    //进入页面直接获取应用
    ApplicationManagement() {
      console.log("进入页面就要执行了");
      let Appyx = {};
      ApplicationManagement(Appyx).then(res => {
        this.application = res.data;
        console.log(res.data[0].id)
        this.yiid = res.data[0].id;
      });
      
    },
    totalAssets(){
     console.log("总资产")
     let zongzc ={}
     totalAssets(zongzc).then(res =>{
       this.shuju=res.data.payWallet
      console.log(res.data.payWallet)
     })
    },
      //进入页面就要执行的事件
    DoItNow(){
      // this.yiid =yiid;
      let xianr ={
        productId: this.yiid,
        type: 0,
        pageNo: 1,
        pageSize: 10
      }
        AssetDetails(xianr).then(res => {
        this.detailed = res.data.content;
        console.log(res.data.content);
      });
    },
    //点击应用 进行切换
    switchdata(id,index) {
      console.log(id);
      this.f1=index
      this.productId = id;
      let zican = {
        productId: id,
        type: this.type,
        pageNo: 1,
        pageSize: 10
      };
      AssetDetails(zican).then(res => {
        this.detailed = res.data.content;
        console.log(res.data.content);
      });
    },
    
    // 点击流水进行切换
    switchdatflowingWatera(y) {
      this.type = y;
      this.f2=y;
      let liushui = {
        productId: this.productId,
        type: y,
        pageNo: 1,
        pageSize: 10
      };
      AssetDetails(liushui).then(res => {
        this.detailed = res.data.content;
        console.log(res.data.content);
      });
    },
    //渲染数据
xuanrshuju(){
 
  console.log("开始渲染我的数据了")
  let xingxi ={};
  essentialInformation(xingxi).then(res =>{
    console.log(res)
       this.daili ==res.data.userInfo.userLevel;
   
  
  })
},
    //去充值
    Recharge() {
      this.modal2 = true;
      console.log("去充值");
      let wallet = {};
      WalletManagement(wallet).then(res => {
        console.log(res.data[0].address);
        this.address = res.data[0].address;
      });
    },
    //去提现弹窗
    Withdrawal() {
      console.log("去提现");
      this.modal3 = true;
    }
  },

  components: {
    APPHeader //顶部引入
  }
};
</script>


