<script>
import APPHeader from '../../src/header'//顶部引入
import { IndustryInformation, RegionalInformation,CreateApp} from "@/api/user";
export default {
  name: "index",
  data() {
    return {
      information:1, //默认状态为1
      hyxinxi: [], //行业信息
      diquxinxi: [], //地区信息
      modal2: false, //登录弹窗
      single: true,//上传无界通默认勾选
      name:"",//应用名称中文
      synopsis:"",//中文简介
      showWjt:1,//0是默认不展示无界通。1是展示
      engName:"",//应用名称英文
      engSynopsis:"",//英文简介
      appInfoType:"",//类型
      wmLogo:"",//水印logo
      hdLogo:"",//高清logo
      showLocation:1,//上架地区0全部，1国内，2国外
      iosUrl:"",//IOS下载地址
      bundleId:"",//IOS 用
      bundleIdTest:"",//IOS 用
      webUrl:"",//H5用
      androidUrl:"",//安卓地址
      sign:"",//安卓签名
      packetName:"",//安卓包名
      payPwd:"",//支付密码
      
      };
  },
  mounted() {
    //行业信息
    this.IndustryInformation();
    //地区信息
    this.RegionalInformation();
  },
  methods: {
    //行业信息
    IndustryInformation() {
      let params = {};
      IndustryInformation(params).then(res => {
        this.hyxinxi = res.data;
      });
    },
    //地区信息
    RegionalInformation() {
      let params = {};
      RegionalInformation(params).then(res => {
        console.log(res.data.country.name)
        console.log(res.data)
        this.diquxinxi = res.data;
      });
    },
    nextstep(){
      console.log("下一步")
      this.information = 2;
    },
    SubmitComplete(){
 
      console.log("完成提交")
      let params ={
        name:this.name,
        synopsis:this.synopsis,
        showWjt:1,
        engName:this.engName,
        engSynopsis:this.engSynopsis,
        appInfoType:1,
        wmLogo:this.wmLogo,
        hdLogo:this.hdLogo,
        showLocation:1,
        webUrl:this.webUrl,
        iosUrl:this.iosUrl,
        bundleId:this.bundleId,
        bundleIdTest:this.bundleIdTest,
        androidUrl:this.androidUrl,
        sign:this.sign,
        packetName:this.packetName,
        payPwd:this.payPwd,

      };
      console.log(params)
     CreateApp(params).then (res =>{
       console.log("data")
     if (res.code ==0){
      // alert("创建成功")
       this.modal2 =false;
        this.information = 3;
    //    alert(res.message)
    //    console.log("500了")
     }
     })  
    },
      handleSuc1(response, file, fileList) {
      this.wmLogo = response.url
    },
        handleSuc2(response, file, fileList) {
      this.hdLogo = response.url
    },
    huqitc(){
      console.log("点击了弹窗")
       this.modal2 =true;
    },
    gouxkck(){
       console.log("ddd")
      this.single =! this.single
      console.log("ddd")
      if(this.single =true){
        this.showWjt=1
      }else{
        this.showWjt=2
      }
      console.log(this.showWjt)
      
    }

    
  },
  components: {
    APPHeader //顶部引入
  }
};
</script>

