            <div class="inputsryzm">
              <input type="text" v-model="smsCode" />
               <div class='modifyYZM' @click="Obtain">
              <li>获取验证码</li>
            </div>
            </div>
           
            <div class="tishi">
              <li>请到邮箱查看并填写验证码，5分钟有效</li>
            </div>
          </div>
        </div>
        <!-- 确定按钮 -->
        <div class="confirmbutton">
          <button @click="Determine">确认</button>
        </div>
      </div>
 
      <!--====================
      =========支付密码修改======= -->
       <div class="youmodify"  v-show="Password==2">
        <!-- 原密码 -->
        <div class="inputPassword">
          <div class="nameming">
            <ul>
              <li>支付原密码</li>
            </ul>
          </div>
          <div class="xininput">
            <div class="inputsr">
              <input type="text" v-model="orgPwd" />
            </div>
