              <li>输入支付原密码</li>
            </div>
          </div>
        </div>
        <!-- 重复密码 -->
        <div class="inputPassword" >
          <div class="nameming">
            <ul>
              <li>支付密码</li>
            </ul>
          </div>
          <div class="xininput">
            <div class="inputsr">
              <input type="text" v-model="newPwd" />
            </div>
            <div class="tishi">
              <li>支付密码</li>
            </div>
          </div>
        </div>
        <!-- 验证码 -->
        <div class="inputPassword">
          <div class="nameming">
            <ul>
              <li>确定支付密码</li>
            </ul>
          </div>
          <div class="xininput">
            <div class="inputsryzm">
              <input type="text" v-model="oldPwd" />
               <!-- <div class='modifyYZM' @click="Obtain"> -->
              <!-- <li>获取验证码</li> -->
            </div>
            </div>
           
            <div class="tishi">
              <li>请确定支付密码</li>
            </div>
          </div>
           <!-- 确定按钮 -->
        <div class="confirmbutton">
          <button @click="ChangePayment">确认</button>
        </div>
        </div>
      </div>
    </div>

</template>

<script>
import APPHeader from '../../src/header'//顶部引入
import {Obtain ,XGLoginPassword,ChangePayment} from "../../src/api/user"//接口引入
export default {
  name: "index",
  data() {
    return {
     Password:1,//默认1
     newPwd:"",//新密码
     oldPwd:"",//旧密码
     smsCode:"",//验证码

    //  支付修改密码
    orgPwd:'',//原密码
    newPwd:"",//重复密码
    oldPwd:"",//确认密码
    };
  },
  methods: {
    Obtain(){
      console.log("点击了获取验证码")
     let MobilePhoneCode = {
        account: "13510525705",
        type: 5,
        // imgCode: this.imgCode,
        area: 86
      };
      Obtain(MobilePhoneCode).then(res => {
        alert("获取成功")
      });
    },
    //修改登录密码 
   Determine(){
     let mimabao ={
       newPwd:this.newPwd,
       oldPwd:this.oldPwd,
       smsCode:this.smsCode
     }
     console.log(mimabao)
     XGLoginPassword(mimabao).then(res =>{
       console.log(res.code)
       if(res.code ==0){
           this.$Notice.success({
            title: "修改成功"
          });
          location.reload(); //刷新页面
       }
     })
   },
  //  修改支付密码
   ChangePayment(){
     let zfmimbao ={
      orgPwd:this.orgPwd,
      newPwd:this.newPwd,
      oldPwd:this.oldPwd
     }
   ChangePayment(zfmimbao).then(res =>{
     console.log(res.code)
      if(res.code ==0){
           this.$Notice.success({
            title: "修改成功"
          });
          location.reload(); //刷新页面
       }else{
         console.log(res.code)
       }
   })
   },
   denglu(){
     console.log("登录")
     this.Password =1;
   },
   zhifu(){
     console.log("支付")
     this.Password =2
   }
  },
  components: {
    APPHeader //顶部引入
  },
};
</script>

