import Vue from 'vue'
import Router from 'vue-router'
import index from '@/index/index'  //首页
import ChangePassword from '@/ChangePassword/index' //修改密码
import EssentialInformation from '@/EssentialInformation/index'//基本信息
import MyAssets from '@/MyAssets/index'//我的资产
import ApplicationManagement from '@/ApplicationManagement/index'//应用管理
import register from '@/register/index'// 注册
import OrderManagement from '@/OrderManagement/index'//订单管理
import WalletManagement from '@/WalletManagement/index'//钱包管理
import Agreement from '@/Agreement/index'//协议
import test from '@/test/index'//测试
import CreateApp from '@/CreateApp/index' //创建应用
import ImproveInformation from '@/ImproveInformation/index'//完善商家信息







Vue.use(Router)

export default new Router({
  routes: [
    { path: '/', redirect: '/src/index/index.vue' },
    {
      path: '/src/index/index.vue',
      name: 'index',//首页
      component: index
    },
    {
      path:'/src/MyAssets/index.vue',
      name:'MyAssets',//我的资产
      component:MyAssets
    },
    {
    path:'/src/EssentialInformation/index.vue',
    name:'EssentialInformation',//基本信息
    component:EssentialInformation
    },
    {
      path:'/src/ChangePassword/index.vue',
      name:'ChangePassword',//修改密码
      component:ChangePassword
    },
    {
      path:'/src/ApplicationManagement/index.vue',
      name:'ApplicationManagement',//应用管理
      component:ApplicationManagement
    },
    {
      path:'/src/register/index.vue',
      name:'register',//注册
      component:register
    },
    {
      path:'/src/OrderManagement/index.vue',
      name:'OrderManagement',//订单管理
      component:OrderManagement
    },
    {
      path:'/src/WalletManagement/index.vue',
      name:'WalletManagement',//钱包管理
      component:WalletManagement
    },
    {
     path:'/src/Agreement/index.vue',
     name:'Agreement',//协议
     component:Agreement
    },
    {
      path:'/src/test/index.vue',
      name:'test',//测试
      component:test
    },
  
    {
      path:'/src/CreateApp/index.vue',
      name:'CreateApp',//创建应用
      component:CreateApp
    },
    {
     path:'/src/ImproveInformation/index.vue',
     name:"ImproveInformation",//完善商家信息
     component:ImproveInformation
    },
    
    

  
  
   
   
 
  
  ]
})



