<script>
import APPHeader from "../../src/header"; //顶部引入
import {
  ApplicationManagement,
  ApplicationData,
  DeleteApp,
  DeterminePayment,
  See,
  placeOrder,
  PurchaseRecord
} from "@/api/user";
export default {
  name: "index",
  data() {
    return {
      shuju: [],
      djcha: {},
      purchaseAPP: [],
      purchaseAPP3: [],
      modal2: false,
      modal3: false,
      modal4: false,
      modal7: false,
      payPwd: "",
      id: "",
      GenerateOrder: []
    };
  },
  mounted() {
    this.application();
  },
  methods: {
    application() {
      console.log("ddddd");
      let Appyx = {};
      ApplicationManagement(Appyx)
        .then(res => {
          this.shuju = res.data;
        })
        // .catch(error => {
        //   // this.$Message.error("error"); 先断开 屏幕是黑的你别动，我来
        //   this.$Message.error('This is an error tip ');
          
        // });
    },
    //查看弹窗
    See(e) {
      console.log("chakan");
      this.modal2 = true;
      console.log(e);
      let cha = {
        productId: e
      };
      console.log(cha);
      See(cha).then(res => {
        this.djcha = res.data.product;
        console.log(res.data.product);
        
      });

      // id= this.e;
    },
    //成为代理商弹窗
    BecomeAnAgent() {
      console.log("成为代理商弹窗");
      this.modal3 = true;
    },
    //去升级
    ToUpgrade() {
      console.log("去升级");
      this.modal4 = true;
      let zsapp = {};
      ApplicationData(zsapp).then(res => {
        // configList 普通用户展示的
        console.log(res.data.configList[(0, 2)]);
        this.purchaseAPP = res.data.configList;
        this.purchaseAPP3 = res.data.configList[3];
        //proxyConfigs代理商套餐
        console.log(res.data.proxyConfigs);
      });
      PurchaseRecord(zsapp).then(res => {
        // console.log(res.data[].buyMeal)
      });
    },
    //删除应用
    DeleteApp(d) {
      // console.log("删除应用")
      let shan = {
        id: d
      };
      console.log(shan);
      DeleteApp(shan).then(res => {
        if (res.code == 0) {
          this.modal2 = false;
          location.reload(); //刷新页面
        }
      });
    },
    //生成订单
    BuyNow(OrderID) {
      // console.log("立即购买")
      let fasong = {
        applyConfigId: OrderID
      };
      placeOrder(fasong).then(res => {
        console.log("res");
        if (res.code == 0) {
          this.modal7 = true;
          this.GenerateOrder = res.data.orderId;
        }
      });
    },
    //立即去购买的
    Gpurchase() {
      // console.log("立即购买按钮")
      let danhao = {
        orderId: this.GenerateOrder,
        payPwd: this.payPwd
      };
      console.log(danhao);
      DeterminePayment(danhao)
        .then(res => {
          if (res.code == 0) {
            this.modal7 = false;
            this.$Notice.success({
              title: "购买成功"
            });
            location.reload(); //刷新页面
          } else {
            this.modal7 = false;
            this.$Notice.success({
              title: "错误"
            });
          }
        })
        .catch(error => {
          //这样不会执行 我没登录 没购买
          console.log(error);
        });
    }
  },
  components: {
    APPHeader //顶部引入
  }
};
</script>

