<template>
  <div class="container">
    <!-- ===========
      ======订单管理=====
    ==================-->
    <!-- 引入的顶部 -->
      <APPHeader  :flag="4"></APPHeader>
    <!-- 应用管理 -->
    <div class="BackgroundMap">
      <div class="BackgroundTitle">
        <li>订单管理</li>
      </div>
    </div>
    <!-- 内容展示 -->
    <div class="ClassCapacityManagement">
      <div class="leiTop">
        <div class="search">
          <div class="searchinput">
            <input type="text" />
          </div>
          <div class="searchbutton">
            <button>搜索</button>
          </div>
        </div>
      </div>

      <!-- 信息类名 -->
      <div class="Applicationclassname">
        <!-- 全选 -->
        <div class="whole">
          <input type="checkbox" />
        </div>
        <div class="applicationName">
          <li>应用名称</li>
        </div>
        <div class="orderNumber">
          <li>订单号</li>
        </div>
        <div class="business">
          <li>业务</li>
        </div>
        <div class="PaymentTime">
          <li>付款时间</li>
        </div>
        <div class="PaymentAmount">
          <li>付款金额</li>
        </div>
        <div class="ServiceCharge">
          <li>手续费</li>
        </div>
        <div class="operation">
          <li>操作</li>
        </div>
      </div>

      <!-- 信息数据 -->
      <div class="datacontainer"  v-for="(item ,index) in shuju "  v-bind:key="item.id">
        <div class="gxkinput">
          <input type="checkbox" />
        </div>
        <div class="Appname">
          <li>{{item.id}}</li>
        </div>
        <div class="OddNumbers">
          <li>{{item.orderId}}</li>
        </div>
        <div class="Recharge">
          <li>{{item.payAddress}}</li>
        </div>
        <div class="time">
          <li>{{item.payTime}}</li>
        </div>
        <div class="money">
          <li>{{item.payTime}}</li>
        </div>
        <div class="HandlingCharges">
          <li>1 KTCH</li>
        </div>
        <div class="CopyOrder">
          <li>复制订单号</li>
        </div>
      </div>


 <Page :total="tiaoshu" show-total></Page>
    </div>

  </div>
</template>

<script>
import APPHeader from '../../src/header'//顶部引入
import {OrderManagement } from "@/api/user";
export default {
  name: "index",
  data() {
    return {
     shuju:[],
     tiaoshu:'',
    };

  },
    //进入页面就执行
  mounted(){
   this.OrderManagement();
  },
  methods:{
   OrderManagement(){
     console.log("进入页面了 要执行了")
     let whole ={};
     OrderManagement(whole).then(res =>{
       console.log(res.data.length)
       this.shuju =res.data;
       this.tiaoshu =res.data.length
     })
   }
  },


 components:{
     APPHeader,//顶部引入
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.BackgroundMap {
  height: 170px;
  width: 100%;
  background-image: url(../assets/bg.png);
}
.BackgroundTitle {
  width: 1200px;
  margin: 0 auto;
  position: relative;
  top: 60px;
}
.BackgroundTitle li {
  font-size: 36px;
  font-family: PingFangSC-Semibold, PingFang SC;
  font-weight: 600;
  color: rgba(255, 255, 255, 1);
  line-height: 50px;
  float: left;
}
.BackgroundTitle button {
  float: right;
  width: 108px;
  height: 40px;
  border-radius: 5px;
  border: 1px solid rgba(255, 255, 255, 1);
  font-size: 14px;
  font-family: PingFangSC-Medium, PingFang SC;
  font-weight: 500;
  color: rgba(255, 255, 255, 1);
  line-height: 20px;
  background-color: transparent;
}
.ClassCapacityManagement {
  width: 1200px;
  /* height: 800px; */
  margin: 0 auto;
}
.leiTop {
  width: 1200px;
  height: 76px;
  padding-top: 40px;
}
.search {
  width: 415px;
  height: 36px;
  background: #ffffff;
}
.searchinput {
  width: 330px;
  height: 30px;
  background: deeppink;
  float: left;
}
.searchinput input {
  width: 100%;
  height: 100%;
}
.searchbutton {
  width: 85px;
  height: 36px;
  background: #318cff;
  float: left;
}
.searchbutton button {
  width: 100%;
  height: 100%;
  background: #318cff;
  border: 0;
  background-color: transparent;
  outline: none;
  color: #ffffff;
}
.Applicationclassname {
  height: 56px;
  width: 1200px;
  background: rgba(245, 245, 245, 1);
}
.whole {
  height: 36px;
  width: 42px;
  float: left;
  padding-top: 20px;
  padding-left: 30px;
}
.whole input {
  width: 16px;
  height: 16px;
}
.applicationName {
  width: 197px;
  height: 38px;
  float: left;
  padding-top: 18px;
}
.Applicationclassname li {
  font-size: 14px;
  font-family: PingFangSC-Regular, PingFang SC;
  font-weight: 400;
  color: rgba(110, 110, 110, 1);
  line-height: 20px;
}
.orderNumber {
  width: 182px;
  height: 38px;
  float: left;
  padding-top: 18px;
}
.business {
  width: 174px;
  height: 38px;
  float: left;
  padding-top: 18px;
}
.PaymentTime {
  width: 183px;
  height: 38px;
  float: left;
  padding-top: 18px;
}
.PaymentAmount {
  width: 153px;
  height: 38px;
  float: left;
  padding-top: 18px;
}
.ServiceCharge {
  width: 138px;
  height: 38px;
  float: left;
  padding-top: 18px;
}
.operation {
  width: 100px;
  height: 38px;
  float: left;
  padding-top: 18px;
}
.datacontainer {
  width: 1200px;
  height: 88px;
  background: #ffffff;
  border: 1px solid rgba(230, 234, 239, 1);
}
.gxkinput {
  width: 42px;
  height: 52px;
  padding-left: 30px;
  padding-top: 36px;
  float: left;
}
.datacontainer input {
  width: 16px;
  height: 16px;
}
.Appname {
  width: 158px;
  height: 55px;
  float: left;
  padding-top: 33px;
}
.datacontainer li {
  font-size: 14px;
  font-family: PingFangSC-Medium, PingFang SC;
  font-weight: 500;
  color: rgba(51, 51, 51, 1);
  line-height: 20px;
}
.OddNumbers {
  width: 208px;
  height: 55px;
  padding-top: 33px;
  float: left;
}
.Recharge {
  width: 154px;
  height: 55px;
  padding-top: 33px;
  float: left;
}
.time {
  width: 210px;
  height: 55px;
  padding-top: 33px;
  float: left;
}
.money {
  width: 155px;
  height: 55px;
  padding-top: 33px;
  float: left;
}
.HandlingCharges {
  width: 138px;
  height: 55px;
  padding-top: 33px;
  float: left;
}
.CopyOrder {
  width: 105px;
  height: 55px;
  padding-top: 33px;
  float: left;
}
.CopyOrder li {
  font-size: 14px;
  font-family: PingFangSC-Medium, PingFang SC;
  font-weight: 500;
  color: rgba(49, 140, 255, 1);
  line-height: 20px;
}
</style>



