<template>
  <div class="container">
    <!-- 引入的顶部 -->
    <APPHeader></APPHeader>
     <div class='hezi'>
      <div class='Titlename'>
          <li>完善商家资料</li>
      </div>
      <div class='Booth'></div>

      <div class='sfname'>
          <div class='zuoname'>
              <li>姓名</li>
          </div>
          <div class='youname'>
            <input type="text" v-model="realName">
            <li>请输入身份证姓名</li>
          </div>
      </div>
        <div class='sfname'>
          <div class='zuoname'>
              <li>身份证号码</li>
          </div>
          <div class='youname'>
            <input type="text" v-model="idCard">
            <li>请输入身份证号码</li>
          </div>
      </div>

      <div class='imgidentity'>
       <div class='zuoname'>
              <li>身份证正面</li>
          </div>
        <div class='youidentity'>
            <div class='imgschuan'>
                  <Upload :with-credentials='true' action="http://192.168.6.37/pay-business/util/upload" :on-success="handleSuc1">
                  <i-button type="text" v-model="wmLogo"  <img src="../assets/add.png" alt="" ></i-button>
                </Upload>
            </div>
             <div class='chakan'>
                 <li>查看示范</li>
             </div>
        </div>
      </div>

          <div class='imgidentity'>
       <div class='zuoname'>
              <li>身份证背面</li>
          </div>
        <div class='youidentity'>
            <div class='imgschuan'>
                  <Upload :with-credentials='true' action="http://192.168.6.37/pay-business/util/upload" :on-success="handleSuc2">
                  <i-button type="text" v-model="wmLogo"  <img src="../assets/add.png" alt="" ></i-button>
                </Upload>
            </div>
             <div class='chakan'>
                 <li>查看示范</li>
             </div>
        </div>
      </div>

          <div class='imgidentity'>
       <div class='zuoname'>
              <li>手持身份证</li>
          </div>
        <div class='youidentity'>
            <div class='imgschuan'>
                  <Upload :with-credentials='true' action="http://192.168.6.37/pay-business/util/upload" :on-success="handleSuc3">
                  <i-button type="text" v-model="wmLogo"  <img src="../assets/add.png" alt="" ></i-button>
                </Upload>
            </div>
             <div class='chakan'>
                 <li>查看示范</li>
             </div>
        </div>
      </div>

      <div class='tijiaoann'>
          <button @click="chuansong">提交</button>
      </div>
     </div>  
    
  </div>
</template>

<script>
import APPHeader from '../../src/header'//顶部引入
import {ImproveInformation } from "@/api/user";
export default {
  name: "index",
  data() {
    return {
     realName:"",//姓名
     idCard:"",//身份证
     frontPhoto:"",//身份证正面
     backPhoto:"",//身份证反面
     holdPhoto:"",//手持身份证
    };
  },
  methods:{
   chuansong(){
     console.log("点击了提交")
     let params ={
       realName:this.realName,
       idCard:this.idCard,
       frontPhoto:this.frontPhoto,
       backPhoto:this.backPhoto,
       holdPhoto:this.holdPhoto
     };
     console.log(params)
     ImproveInformation(params).then(res =>{
       console.log(res)
     })
   },
   
    handleSuc1(response, file, fileList) {
      this.frontPhoto = response.url
    },
    handleSuc2(response, file, fileList) {
      this.backPhoto = response.url
    },
    handleSuc3(response, file, fileList) {
      this.holdPhoto = response.url
    },
 
  },
  components:{
     APPHeader,//顶部引入
  },

};
</script>

<style scoped>
.hezi{
  width: 1200px;
  height: 800px;
  margin: 0 auto;
}
.Titlename {
    height: 45px;
    width: 1170px;
    background: #f4f4f4;
    padding-left: 30px;
    padding-top: 15px;
}
.Titlename li{
    font-size:20px;
font-family:PingFangSC-Regular,PingFang SC;
font-weight:400;
color:rgba(51,51,51,1);
line-height:28px;
}

.Booth{
    height: 100px;
}
.sfname{
    width: 444px;
    height: 100px;
    margin: 0 auto;
}
.zuoname{
    width: 104px;
    height: 89px;
    float: left;
    padding-top: 11px;
}
.youname{
    width: 340px;
    height: 100px;

    float: left;
}
.youname input{
    width: 335px; 
    height: 42px;
    border:1px solid rgba(230,234,239,1);

}
.imgidentity{
    height: 107px;
    width: 444px;
    margin: 0 auto;
}
.youidentity{
    width: 335px;
    height: 80px;
    float: left;
}
.imgschuan{
    width: 78px;
    height: 71px;
    float: left;
}
.chakan{
    height: 17px;
    width: 200px;
    float: left;
    padding-top: 55px;
    padding-left: 5px;
}
.chakan li{
    font-size:12px;
font-family:PingFangSC-Regular,PingFang SC;
font-weight:400;
color:rgba(49,140,255,1);
line-height:17px;
}
.tijiaoann{
    height: 38px;
    width: 210px;
    margin: 0 auto;
    padding-top: 50px;
}
 .tijiaoann button{
     width:210px;
height:38px;
background:rgba(49,140,255,1);
border-radius:4px;

 }
</style>



