// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import ViewUI from 'view-design'//ViewUI组件
import 'view-design/dist/styles/iview.css' //ViewUI组件

import '@/style/index.less' // global css ViewUI组件
import axios from 'axios'
import VueAxios from 'vue-axios'
import VueI18n from 'vue-i18n'    //语言切换
import Clipboard from 'v-clipboard' //复制功能

Vue.use(VueI18n)

 Vue.use(Clipboard)
Vue.use(VueAxios, axios)
Vue.use(ViewUI)

Vue.config.productionTip = false

// axios.defaults.withCredentials = true;

const i18n = new VueI18n({
  locale:  localStorage.getItem('lang') ? localStorage.getItem('lang') : 'zh',  // 语言标识
  messages: {
      'cn': require('./common/lang/zh'),
      'en': require('./common/lang/en')
  }
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  i18n,
  router,
  components: { App },
  template: '<App/>'
})
