<template>
  <div class="container">
    <!-- 顶部 -->
    <div class="Top_container">
      <div class="Middle">
        <div class="logoimg">
          <div class="logo">
            <img src="../assets/logo.png" alt />
          </div>
          <div class="title">
            <li>无界通商家平台</li>
          </div>
        </div>

        <div class="Navigation">
          <div class="Navigationzuo">
            <ul>
              <li style=" color: #318CFF;">首页</li>
              <router-link :to="{path:'/src/ApplicationManagement/index.vue'}">
                <li>应用管理</li>
              </router-link>
              <router-link :to="{path:'/src/WalletManagement/index.vue'}">
                <li>钱包管理</li>
              </router-link>

              <router-link :to="{path:'/src/OrderManagement/index.vue'}">
                <li>订单管理</li>
              </router-link>
              <router-link :to="{path:'/src/EssentialInformation/index.vue'}">
                <li>个人中心</li>
              </router-link>
               <div class="zwenqieh"  @click="dianjiqieh">
                 <div class='dianjannniu'>
                   <li>中文</li>
                   <img src="../assets/ic_sanjiaoxing@2x.png" alt />
                 </div>
                  <div class='tanczhongwen' v-if="show">
                    <li @click="changLang(1)">中文</li>
                    <li  @click="changLang(2)">English</li>
                  </div>
                  
               </div>

               
              
              <!-- <Dropdown trigger="click" class="zwenqieh">
                <li class="qihenem">中文</li>
                <img src="../assets/ic_sanjiaoxing@2x.png" alt />
                <div class="translate" slot="list">
                  <li style="#FFFFFF" @click="changLang(1)">中文</li>
                  <li style="#FFFFFF" @click="changLang(2)">English</li>
                </div>
              </Dropdown> -->
            </ul>
          </div>
         
          <!-- 登录 注册 -->
          <div class="signIn" v-show="information==1">
            <button class="signInbutton" @click="testApi">{{$t("login.title")}}</button>
            <!-- <button class="registerbutton">注册</button> -->
          </div>
          <!-- 登录的样式 -->
          <div class='signIn' v-show="information==2" @click="Sign">
                   <div class='dianjannniu'>
                   <li class="liname" >{{this.realName}}</li>
                   <img src="../assets/ic_sanjiaoxing@2x.png" alt />
                 </div>
                   <div class='displaySignOut' v-if="tuic">
                    <li>切换账户</li>
                    <li  @click="SignOut">退出登录</li>
                  </div>
          </div>
          
        </div>
      </div>
