<script>
import { login, SignOut,getPath,LogInOrNot,essentialInformation} from "@/api/user";

export default {
  name: "index",
  data() {
    return {
      account: "", //用户名
      password: "", //密码
      imgCode: "", //验证码
      modal10: false, //登录弹窗
      realName: "", //用户名称
      erweima: "", //二维码
      shuju: [],
      information: 1,//登录信息
      tuic:false, //退出登录
      show:false,//切换语言
    };
  },
  mounted() {
    this.getPath();
    this.LogInOrNot();
     this.xuanrshuju();
  },
  methods: {
    // 切换中英文
    changLang(f) {
      console.log(f);
      let lang = "";
      if (f == 1) {
        this.$i18n.locale = "cn";
        lang = "cn";
      } else {
        this.$i18n.locale = "en";
        lang = "en";
      }
      localStorage.setItem("lang", lang);
    },
    //登录弹窗
    testApi() {
      this.changeERima();
      this.modal10 = true;
    },
    changeERima() {
      this.erweima =
        "http://192.168.6.37/pay-business/drawImage?t=" +
        Date.parse(new Date());
    },
    //执行进入首页获取图片
    getPath() {
      let params ={};
      getPath (params).then(res =>{
        console.log(res)
         this.shuju = res.data.infoList

      })
    },
 
    
    // 退出登录
    SignOut() {
      let params = {};
      SignOut(params).then(res => {
        this.$Notice.success({
          title: "退出成功"
        });
        location.reload(); //刷新页面  
      });
    },
    //+++++++登录信息
    login() {
      let params = {
        account: this.account,
        password: this.password,
        imgCode: this.imgCode
      };
      login(params)
        .then(res => {
          console.log(res.data.name);
          //  debugger 
          this.realName = res.data.name;
          this.modal10 = false;
          this.information = 2;
          this.$Notice.success({
            title: "登录成功"
          });
        })
        .catch(err => {
          this.$Notice.error({
            title: err.message
          });
        });
    },
           //渲染数据
xuanrshuju(){
  console.log("开始渲染我的数据了")
  let xingxi ={};
  essentialInformation(xingxi).then(res =>{
    console.log(res)
    this.realName=res.data.business.name
    console.log(res.data.business.name)
  })
},
//判断是否登录
    LogInOrNot() {
      let params={};
    
     LogInOrNot(params).then(res =>{
            console.log(res.code);
            console.log(res)
            
          if (res.code == 0) {
            // this.realName = "登录后的样式";
            this.information = 2;
            console.log("0事件执行");
          } else {
            this.information = 1;
          }
     })
    },
    dianjiqieh(){
      console.log("dianjile")
     this.show = !this.show;
     this.tuic =false;
    },
    Sign(){
      this.tuic =!this.tuic;
      this.show =false;
    }

  }
};
</script>

