<template>
  <div class="container">
    <!-- 引入的顶部 -->
    <APPHeader></APPHeader>



     <div class='hezi'>
          <div>
            <li>姓名</li>
            <input type="text" v-model="realName" />
          </div>
        <div>
          <li>身份证</li>
          <input type="text" v-model="idCard" />
        </div>
        <div>
          <li>正面照</li>
           <Upload :with-credentials='true' action="http://192.168.6.37/pay-business/util/upload" :on-success="handleSuc1">
        <i-button type="ghost" v-model="frontPhoto" icon="ios-cloud-upload-outline">正面照</i-button>
    </Upload>
        </div>
        <div>
          <li>背面照</li>
           <Upload :with-credentials='true' action="http://192.168.6.37/pay-business/util/upload" :on-success="handleSuc2">
        <i-button type="ghost" v-model="backPhoto" icon="ios-cloud-upload-outline">背面照</i-button>
    </Upload>
        </div>
        <div>
          <li>手持照</li>
           <Upload :with-credentials='true' action="http://192.168.6.37/pay-business/util/upload" :on-success="handleSuc3">
        <i-button type="ghost" v-model="holdPhoto" icon="ios-cloud-upload-outline">手持照</i-button>
    </Upload>
        </div>
   

     <div class='ddddd'>
       <div>{{copys}}</div>
       

<div v-clipboard:copy="copys" v-clipboard:success="copy">复制</div>
     </div>

 
        <div>
          <button @click="chuansong">提交</button>
        </div>
     
     </div>
     




  </div>
</template>

<script>
import APPHeader from '../../src/header'//顶部引入
import {ImproveInformation } from "@/api/user";
export default {
  name: "index",
  data() {
    return {
     realName:"",//姓名
     idCard:"",//身份证
     frontPhoto:"",//身份证正面
     backPhoto:"",//身份证反面
     holdPhoto:"",//手持身份证
     copys:{
      
     },
    };
  },
  methods:{
   chuansong(){
     console.log("点击了提交")
     let params ={
       realName:this.realName,
       idCard:this.idCard,
       frontPhoto:this.frontPhoto,
       backPhoto:this.backPhoto,
       holdPhoto:this.holdPhoto
     };
     console.log(params)
     ImproveInformation(params).then(res =>{
       console.log(res)
     })
   },
   
      copy(){

        console.log('成功复制到粘贴板！')

    },

    handleSuc1(response, file, fileList) {
      this.frontPhoto = response.url
    },
    handleSuc2(response, file, fileList) {
      this.backPhoto = response.url
    },
    handleSuc3(response, file, fileList) {
      this.holdPhoto = response.url
    },
      handleBeforeUpload (file) {
      let promise = new Promise(resolve => {
        this.$nextTick(function () {
          let _this = this
          let reader = new FileReader()
          // תbase64
          reader.onload = function (e) {
            _this.fileUrl = e.target.result // img里可以显示未上传的图片文件以供预览
          }
          reader.readAsDataURL(file)
          resolve(true)
        })
      })
      return promise
    }

  },
  components:{
     APPHeader,//这里
  },

};
</script>

<style scoped>
.hezi{
  width: 1200px;
  height: 800px;
  margin: 0 auto;
  background: rosybrown;
}
.ceshi{
  width: 50px;
  height: 30px;
  background: red;
}
.ddddd{
  width: 300px;
  height: 300px;
  background: blueviolet;
}
</style>



