<template>
  <div class="container">
    <!-- 顶部 -->
    <div class="Top_container">
      <div class="Middle">
        <div class="logoimg">
          <div class="logo">
            <img src="../src/assets/logo.png" alt />
          </div>
          <div class="title">
            <li @click="LogInOrNot(realName)">无界通商家平台</li>
          </div>
        </div>
        <div class="Navigation">
          <div class="Navigationzuo">
            <!-- 这个点击变色 li 给我看下，你这个，其他页面是怎么用的这个公共的 好 -->
            <ul>
              <router-link :to="{path:'/src/index/index.vue'}">
                <li @click="toPath(1)" :class="{active: flag==1 }">{{$t("home.title")}}</li>
              </router-link>
              <router-link :to="{path:'/src/ApplicationManagement/index.vue'}">
                <li @click="toPath(2)" :class="{active: flag==2 }">{{$t("application.title")}}</li>
              </router-link>
              <router-link :to="{path:'/src/WalletManagement/index.vue'}">
                <li @click="toPath(3)" :class="{active: flag==3 }">钱包管理</li>
              </router-link>
              <router-link :to="{path:'/src/OrderManagement/index.vue'}">
                <li @click="toPath(4)" :class="{active: flag==4 }">订单管理</li>
              </router-link>
              <router-link :to="{path:'/src/EssentialInformation/index.vue'}">
                <li @click="toPath(5)" :class="{active: flag==5 }">个人中心</li>
              </router-link>
              <div class="zwenqieh" @click="dianjiqieh">
                <div class="dianjannniu">
                  <li>中文</li>
                  <img src="../src/assets/xialtu.png" alt />
                </div>
                <div class="tanczhongwen" v-if="show">
                  <li @click="changLang(1)">中文</li>
                  <li @click="changLang(2)">English</li>
                </div>
              </div>
            </ul>
          </div>
          <!-- 登录 注册 -->
          <div class="signIn" v-show="information==1">
            <button class="signInbutton" @click="testApi">{{$t("login.title")}}</button>
            <!-- <button class="registerbutton">注册</button> -->
          </div>
          <!-- 登录的样式 -->
          <div class="signIn" v-show="information==2" @click="Sign">
            <div class="dianjannniuq">
              <li class="liname">{{this.realName}}</li>
              <img src="../src/assets/xialtu.png" alt />
            </div>
            <div class="displaySignOut" v-if="tuic">
              <li>切换账户</li>
              <li @click="SignOut">退出登录</li>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- 登录弹窗-->
    <Modal v-model="modal10" width="389" height="400" class-name="vertical-center-modal">
      <div class="tanctop">
        <li>登录</li>
      </div>
      <div slot="footer">
        <div class="mimk">
          <div class="Loginaccount">
            <div class="inconimg">
              <img src="../src/assets/zhanghao.png" />
            </div>
            <div class="accountinput">
              <input type="text" v-model="account" placeholder="登录账号" />
            </div>
          </div>
          <div class="Loginaccount">
            <div class="inconimg">
              <img src="../src/assets/mima.png" />
            </div>
            <div class="accountinput">
              <input type="password" v-model="password" placeholder="登录密码" />
            </div>
          </div>

          <div class="VerificationCode">
            <div class="zuoVerification">
              <div class="inconimg">
                <img src="../src/assets/yanzheng.png" />
              </div>
              <div class="shuryanzm">
                <input type="text" v-model="imgCode" placeholder="验证码" />
              </div>
            </div>
            <div class="yanzmimg">
              <img :src="erweima" alt />
            </div>
            <div class="switch" @click="changeERima">
              <li>换一张</li>
            </div>

            <div class="ForgetPassword">
              <li>忘记密码</li>
            </div>

            <div class="nextStep">
              <div class="zuonextStep">
                <button @click="login">下一步</button>
              </div>
              <div class="younextStep">
                <button>注册</button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Modal>
  </div>
</template>

<script>
import { login,essentialInformation} from "@/api/user";
export default {
  name: "index",
  props:{
    flag:{
      type: null,
      default: -1
    }
  },
  data() {
    return {
      account: "", //用户名
      password: "", //密码
      imgCode: "", //验证码
      realName: "", //用户名称
      modal10: false, //登录弹窗
      information: 1, //登录信息
      erweima: "", //二维码
      //这个是假数据
      content: {},
      tuic: false, //退出登录
      show: false, //切换语言
    };
  },
  mounted() {
    this.LogInOrNot();
    this.xuanrshuju();
  },
  methods: {
    toPath(f) {
      console.log(f);
    },
    // 切换中英文
    changLang(f) {
      console.log(f);
      let lang = "";
      if (f == 1) {
        this.$i18n.locale = "cn";
        lang = "cn";
      } else {
        this.$i18n.locale = "en";
        lang = "en";
      }

      localStorage.setItem("lang", lang);
    },
    //登录弹窗
    testApi() {
      // console.log("dianjile");
      this.changeERima();
      this.modal10 = true;
    },
    //获取了验证码
    changeERima() {
      this.erweima =
        "http://192.168.6.37/pay-business/drawImage?t=" +
        Date.parse(new Date());
    },
    //获取登录信息
    login() {
      let params = {
        account: this.account,
        password: this.password,
        imgCode: this.imgCode
      };
      login(params)
        .then(res => {
          console.log(res.data.name);
          //  debugger
          this.realName = res.data.name;
          
          this.modal10 = false;
          this.information = 2;
          this.$Notice.success({
            title: "登录成功"
          });
        })
        .catch(err => {
          this.$Notice.error({
            title: err.message
          });
        });
    },
    //渲染数据
xuanrshuju(){
  console.log("开始渲染我的数据了")
  let xingxi ={};
  essentialInformation(xingxi).then(res =>{
    console.log(res)
    this.realName=res.data.business.name
    console.log(res.data.business.name)
  })
},
    //判断是否登录
    LogInOrNot(e) {
      console.log(e);
      this.axios
        .post("http://192.168.6.37/pay-business/business/getMyLevel")
        .then(homepage => {
          console.log(homepage.data);
          if (homepage.data.code == 0) {
            // this.realName = "这登录后的样式";
            this.information = 2;
            console.log("0事件执行");
          } else {
            this.information = 1;
          }
          console.log(this.realName);
        });
    },

    // 退出登录
    SignOut() {
      // console.log("点击了登录")
      this.axios
        .post("http://192.168.6.37/pay-business/business/logout")
        .then(homepage => {
          this.$Notice.success({
            title: "退出成功"
          });
          location.reload(); //刷新页面
        });
    },
    dianjiqieh() {
      console.log("dianjile");
      this.show = !this.show;
      this.tuic = false;
    },
    Sign() {
      this.tuic = !this.tuic;
      this.show = false;
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.active{
  color: #318CFF !important;
}

.Top_container {
  height: 80px;
  width: 100%;
  background: #ffffff;
}
.Middle {
  width: 1200px;
  height: 80px;
  background: #ffffff;
  margin: 0 auto;
}
.logoimg {
  padding-top: 25px;
  float: left;
}
.logo {
  width: 30px;
  height: 30px;
  /* background: red; */
  float: left;
}
.title {
  padding-top: 4px;
  padding-left: 10px;
  float: left;
}
.title li {
  font-size: 16px;
  font-family: PingFangSC-Regular, PingFang SC;
  font-weight: 400;
  color: rgba(51, 51, 51, 1);
  line-height: 22px;
}

.Navigation {
  float: right;
  padding-top: 28px;
}
.Navigation ul li {
  display: inline;
  font-size: 16px;
  font-family: PingFangSC-Regular, PingFang SC;
  font-weight: 400;
  color: rgba(51, 51, 51, 1);
  margin-left: 25px;
  cursor: pointer;
}

.Navigationzuo {
  float: left;
}

.signIn {
  width: 136px;
  height: 28px;
  float: right;
  padding-left: 25px;
  margin-top: -3px;
}
.signInbutton {
  width: 58px;
  height: 28px;
  background: rgba(49, 140, 255, 1);
  border-radius: 3px;
  font-size: 13px;
  font-weight: 400;
  color: rgba(255, 255, 255, 1);
  /* line-height:22px; */
  border: none;
  float: right;
}
.registerbutton {
  width: 58px;
  height: 28px;
  border: 1px solid rgba(49, 140, 255, 1);
  float: right;
  background: #ffffff;
  color: rgba(49, 140, 255, 1);
  border-radius: 3px;
  border: none;
}

.BottomBar {
  height: 80px;
  width: 100%;
  background: #010619;
}
.BottomBar li {
  height: 22px;
  font-size: 16px;
  font-family: PingFangSC-Semibold, PingFang SC;
  font-weight: 600;
  color: rgba(181, 181, 181, 1);
  line-height: 22px;
  text-align: center;
  position: relative;
  top: 29px;
}
.active {
  color: red;
}
.qihenem {
  font-size: 16px;
  font-family: PingFangSC-Regular, PingFang SC;
  font-weight: 400;
  color: rgba(51, 51, 51, 1);
  line-height: 22px;
  float: left;
}
.zwenqieh img {
  width: 10px;
  height: 5px;
}
.zwenqieh {
  float: right;
}
.translate {
  height: 40px;
  width: 100px;
  background: #000000;
  box-shadow: 0px 2px 6px 0px rgba(0, 0, 0, 0.09);
  border-radius: 5px;
  position: relative;
  top: -30px;
}
.translate li {
  height: 36px;
  font-size: 16px;
  font-family: PingFangSC-Regular, PingFang SC;
  font-weight: 400;
  color: red;
  line-height: 22px;
  text-align: center;
}
.tanctop {
  width: 389px;
  height: 92px;
  background: #ffffff;
  padding-top: 31px;
}
.tanctop li {
  font-size: 20px;
  font-family: PingFangSC-Regular, PingFang SC;
  font-weight: 400;
  color: rgba(51, 51, 51, 1);
  line-height: 28px;
  text-align: center;
}
.mimk {
  height: 308px;
  padding-left: 34px;
  padding-right: 34px;
  background: #ffffff;
}
.Loginaccount {
  width: 100%;
  height: 49px;
  border: 1px solid rgba(230, 234, 239, 1);
  margin-top: 12px;
}
.inconimg {
  width: 41px;
  height: 48px;
  float: left;

  padding-top: 14px;
  padding-left: 11px;
}
.inconimg img {
  width: 20px;
  height: 20px;
  float: left;
}
.accountinput input {
  width: 278px;
  height: 47px;
  float: left;
  outline: none;
  border: 0;
}
.shuryanzm {
  width: 96px;
  height: 49px;

  float: left;
}
.shuryanzm input {
  width: 96px;
  height: 47px;
  float: left;
  outline: none;
  border: 0;
}
.yanzmimg {
  width: 122px;
  height: 48px;
  background: red;
  float: left;
  margin-left: 10px;
}
.yanzmimg img {
  width: 100%;
  height: 100%;
}
.switch {
  width: 48px;
  height: 48px;
  float: left;
  padding-left: 12px;
  padding-top: 16px;
}
.switch li {
  font-size: 12px;
  font-family: PingFangSC-Regular, PingFang SC;
  font-weight: 400;
  color: rgba(51, 51, 51, 1);
  line-height: 17px;
}
.ForgetPassword {
  height: 63px;
  width: 320px;
  float: left;
  padding-top: 24px;
}
.ForgetPassword li {
  float: right;
  font-size: 12px;
  font-family: PingFangSC-Regular, PingFang SC;
  font-weight: 400;
  color: rgba(51, 51, 51, 1);
  line-height: 17px;
}
.nextStep {
  height: 42px;
  width: 320px;
  float: left;
}
.zuonextStep {
  width: 140px;
  height: 42px;
  float: left;
}
.zuonextStep button {
  width: 140px;
  height: 42px;
  background: rgba(49, 140, 255, 1);
  border-radius: 5px;
  font-size: 14px;
  font-family: PingFangSC-Medium, PingFang SC;
  font-weight: 500;
  color: rgba(255, 255, 255, 1);
  line-height: 20px;
}
.younextStep {
  width: 140px;
  height: 42px;
  float: right;
}
.younextStep button {
  width: 140px;
  height: 42px;
  border-radius: 5px;
  border: 1px solid rgba(49, 140, 255, 1);
  font-size: 14px;
  font-family: PingFangSC-Medium, PingFang SC;
  font-weight: 500;
  color: rgba(49, 140, 255, 1);
  line-height: 20px;
}
.liname {
  font-size: 16px;
  font-family: PingFangSC-Regular, PingFang SC;
  font-weight: 400;
  /* color: #333333; */
  line-height: 22px;
  margin-top: 4px;
  float: left;
}
.signIn img {
  width: 10px;
  height: 5px;
  float: left;
  margin-top: 15px;
}
.tuichu {
  height: 80px;
  width: 122px;
  background: #ffffff;
  box-shadow: 0px 1px 4px 0px rgba(0, 0, 0, 0.09);
  border-radius: 5px;
  position: relative;
  top: 10px;
}
.tuichu li {
  height: 36px;
  font-size: 16px;
  font-family: PingFangSC-Regular, PingFang SC;
  font-weight: 400;
  color: #000000;
  line-height: 22px;
  text-align: center;
}
.dianjannniu {
  width: 112px;
}
.tanczhongwen {
  width: 112px;
  height: 80px;
  background: #ffffff;
}
.tanczhongwen li {
  width: 112px;
  height: 40px;
  line-height: 40px;
  color: #000000;
}
.displaySignOut {
  width: 95px;
  height: 80px;
  background: #ffffff;
  position: relative;
  top: 30px;
  padding-left: 15px;
}
.displaySignOut li:nth-child(1) {
  position: relative;
  top: -15px;
}
.displaySignOut li {
  font-size: 16px;
  font-family: PingFangSC-Regular, PingFang SC;
  font-weight: 400;
  color: #000000;
  line-height: 22px;
  height: 40px;
}
</style>



